import React, { useState, useEffect } from "react";
import API from '../utils/API';

const RequestCash = (props) => {
    const [p, setP] = useState(0);
    const [i, setI] = useState(0);
    const [n, setN] = useState(0);
    const [m1, setM1] = useState(0);
    const [m2, setM2] = useState(0);
    const [m3, setM3] = useState(0);
    const [loaner, setLoaner] = useState('');

    useEffect(() => {
      setM1((i * (1 + i) ** n) / ((1 + i) ** n - 1));
    }, [i]);
    useEffect(() => {
      setM2(p * m1);
      setM3(Math.round(p * m1));
    }, [p]);

    const handleSubmit = (e) => {
        e.preventDefault();

        var borrow = {
            username: 'Jason',
            loaner: loaner,
            amount: p,
            interest: i,
            months: n,
        }

        API.borrowMoney(borrow).then((res) => console.log(res));
    }

    const handleLoaner = (e) => {
        setLoaner(e.target.value);
    }

    const handlePrincipal = (e) => {
        setP(parseInt(e.target.value));
    };
    const handleInterest = (e) => {
        setI(parseInt(e.target.value) / 100);
    };
    const handleDuration = (e) => {
        setN(parseInt(e.target.value));
    };

    return (
        <form style={{ color: 'white' }} onSubmit={handleSubmit}>
        <label>
            Loaner:
            <input onChange={handleLoaner}
            className='form-control'
            type='text'
            placeholder='Enter Loaner'
            />
        </label>
        <label>
            Principal:
            <input onChange={handlePrincipal}
                className="form-control"
                type="p"
                placeholder="Enter Value"
                Principal="Amount"
            />
        </label>
        <label>
            Intrest:
            <select onChange={handleInterest}>
            <option value="0" i="">0</option>
            <option value="5" i="">5%</option>
            <option value="10" i="">10%</option>
            <option value="15" i= "">15%</option>
            </select>
        </label>
        <label>
            Duration:
            <select onChange={handleDuration}>
            <option N="Term">6 Months</option>
            <option N="Term">12 Months</option>
            <option N="Term">24 Months</option>
            <option N="Term">36 Months</option>
            </select>
        </label>
        <input type="submit" />
        </form>
    );
};

export default RequestCash;